.login_page {
  max-width: 400px;
  margin: auto;
  color: #444;
  background-color: #ffffff;
  padding: 1rem 2.5rem;
  text-align: center;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.12), 0 10px 10px rgba(0, 0, 0, 0.12);
  font-family: "Montserrat", sans-serif;
  border-radius: 12px;
}

.login_container {
  height: calc(100vh - 100px);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.login_page h2 {
  font-size: 1.5rem;
  letter-spacing: 1.2px;
}

.auth_input {
  background-color: #eee;
  font-family: "Montserrat", sans-serif;
  font-size: 0.8rem;
  font-weight: 200;
  box-sizing: border-box;
  border: none;
  padding: 8px 11px;
  margin: 8px 0;
  border-radius: 4px;
  width: 100%;
  border-bottom: 3px solid transparent;
}

.auth_label {
  font-size: 0.8rem;
}

.auth_input:focus {
  outline: none;
  box-shadow: 0 1rem 2rem rgba(#000, 0.1);
  border-bottom: 3px solid var(--light-green);
}

.auth_input:invalid {
  outline: none;
  box-shadow: 0 1rem 2rem rgba(#000, 0.1);
  border-bottom: 3px solid var(--warning-red);
}

.login_page .row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap-reverse;
}
.login_page .row a {
  margin-left: 0.4rem;
  font-size: 0.8rem;
  color: crimson;
}

.login_page p {
  font-size: 0.8rem;
  margin: 5px 0;
}
.login_page a {
  color: #111;
  font-weight: 600;
}

/* ------------- Forgot Password --------------- */
.fg_pass h2 {
  color: #555;
  text-transform: uppercase;
  text-align: center;
  font-size: 2rem;
  margin: 50px 0;
  letter-spacing: 1.3px;
}
.fg_pass .row {
  max-width: 500px;
  margin: auto;
  padding: 0 10px;
}
.fg_pass .row input {
  width: 100%;
  height: 45px;
  border: 1px solid #ccc;
  outline: none;
  padding: 0 15px;
  border-radius: 3px;
  margin: 5px 0;
}

/*-----Button-----*/
.auth_button {
  border-radius: 20px;
  font-family: "Montserrat", sans-serif;
  border: 1px solid #ff4b2b;
  background-color: #ff4b2b;
  color: #ffffff;
  font-size: 12px;
  font-weight: bold;
  padding: 12px 24px;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: transform 80ms ease-in;
}

.auth_back {
  border-radius: 20px;
  font-family: "Montserrat", sans-serif;
  border: 1px solid #ff4b2b;
  background-color: #ff4b2b;
  color: #ffffff;
  font-size: 7px;
  font-weight: bold;
  padding: 5px 12px;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: transform 80ms ease-in;
}

.auth_button:active {
  transform: scale(0.95);
}

.auth_button:focus {
  outline: none;
}

.auth_button-ghost {
  background-color: transparent;
  border-color: #ffffff;
}

